@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    font-family: Roboto, sans-serif;
}

* {
  padding: 0;
  margin: 0;
}

@layer components {
  .title {
    @apply text-gray-900 dark:text-white font-thin text-2xl text-center uppercase mb-10;
  }
  
  .label {
    @apply block mb-2 text-xs font-bold text-gray-900 dark:text-white uppercase;
  }
  
  .input {
    @apply text-gray-900 dark:text-white bg-white dark:border-slate-400 dark:bg-slate-800 border-2 text-xs rounded-lg outline-none block w-full p-2.5
  }

}


p {
  color: inherit;
}


.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.checkmark__circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 2;
  stroke-miterlimit: 10;
  stroke: #4ade80;
  fill: none;
  animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

.checkmark {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  display: block;
  stroke-width: 2;
  stroke: #fff;
  stroke-miterlimit: 10;
  margin: 10% auto;
  box-shadow: inset 0px 0px 0px #4ade80;
  animation: fill 0.4s ease-in-out 0.4s forwards,
    scale 0.3s ease-in-out 0.9s both;
}

.checkmark__check {
  transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
}

@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes scale {
  0%,
  100% {
    transform: none;
  }

  50% {
    transform: scale3d(1.1, 1.1, 1);
  }
}

@keyframes fill {
  100% {
    box-shadow: inset 0px 0px 0px 30px #4ade80;
  }
}